@import "bourbon/bourbon";
@import "_misc/_media-mixins";
@import "libs";
@import "_misc/_mixins";
@import "_misc/_sprite-svg";
@import "_misc/_vars";
@import "_misc/_adjustment";
@import "_sections/_header";
@import "_sections/_content";
@import "_sections/_footer";

.timeline{
	padding: $gutter*2 0;

	&-item{
		padding: $gutter/2 0;

		&:before{
			content: '';
		}
		
		&-img{
			width: 74px;
			height: 74px;
			border-radius: 50%;
			overflow: hidden;
			border: 5px solid gray;
			position: absolute;
		}

		p{
			margin: 4px 0 0 0;
		}
		span{
			display: block;
		}
	}

}
@import "_media";

