body{
	font-family: "RobotoRegular", sans-serif;
	font-weight: normal;
	font-size: 15px;
	min-width: 320px;
	position: relative;
	line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
	background-color: gray;
}
aside{
	background-color: #fff;
}
body input:focus:required:invalid,
body textarea:focus:required:invalid{
	color: red;
}
body input:required:valid,
body textarea:required:valid{
	color: green;
}
input{
	outline: none;
}

ul, li{
	margin: 0;
	padding: 0;
}
.justify{
	content: '';
	width: 100%;
	height: 0;
	display: inline-block;
	overflow: hidden;
	visibility: hidden;
	line-height: 0;
}
.hidden{
	display: none;
}
