header{
	margin-bottom: 30px;
	background-color: #fff;
}
.header-inner{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.header-menu{
	ul{
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		// align-items: center;

		li{
			margin-left: 25px;
			list-style-type: none;

			a{
				text-transform: uppercase;
			}
		}
	}
}
.header-logo{
	max-width: 300px;
	img{
		max-width: 100%;
	}
}
.sidebar{
	border: 1px solid gray;
	li{
		list-style-type: none;
		// background-color: gray;
		a{
			display: block;
			padding: 10px;
			color: #000;
			text-transform: capitalize;
		}
	}
}